import React from 'react';
    import Helmet from 'react-helmet';

    const css = `body{background:#add8e6;color:red}`;

    export default class HeadCss extends React.Component {
      shouldComponentUpdate() {
        return false;
      }
      render() {
        return (
          <Helmet defer={false}>
            <style>{css}</style>
          </Helmet>
        );
      }
    }


