import React from 'react';
    import Helmet from 'react-helmet';

    const css = `body{background:pink;color:gray}`;

    export default class HeadCss extends React.Component {
      shouldComponentUpdate() {
        return false;
      }
      render() {
        return (
          <Helmet defer={false}>
            <style>{css}</style>
          </Helmet>
        );
      }
    }


